html, body, html *, *, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

body.no-scroll {
  overflow: hidden;
}

.wrapper {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
}
.wrapper main {
  flex: 1;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  cursor: pointer;
  background: transparent;
  border: none;
  color: inherit;
}
button:disabled {
  cursor: unset;
}

span, b {
  color: inherit;
  font: inherit;
}

li {
  list-style: none;
}

input, textarea {
  outline: none;
  border: none;
}

input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  appearance: none;
}

[class*=icon-]::before {
  width: auto;
  margin-left: 0;
  margin-right: 0;
}

* {
  font-family: Montserrat, sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 1;
}

body, html {
  background-color: white;
  color: #4F4F4F;
}

body {
  transition: 0.4s ease;
  opacity: 0;
}
body.loaded {
  opacity: 1;
}

[class*=__container], .container {
  padding: 0 clamp(15px, 0.369rem + 2.27vw, 35px);
  margin: 0 auto;
  max-width: 1280px;
  min-width: 360px;
  height: max-content;
}

.page-slider {
  overflow: hidden;
}
.page-slider .swiper {
  display: flex;
  flex-direction: column;
  touch-action: pan-y;
  height: 100%;
  overflow: hidden;
}
.page-slider .swiper-wrapper {
  display: flex;
  flex-direction: column;
}

.page-slide {
  position: relative;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}
.page-slide [class*=__container] {
  flex: 1;
  width: 100%;
}

.pagination {
  position: fixed;
  z-index: 100;
  top: clamp(15px, 0.568rem + 1.48vw, 28px);
  right: clamp(20px, 0.767rem + 1.93vw, 37px);
  display: flex;
  gap: 6px;
}
.pagination span {
  cursor: pointer;
  border: 1px solid #ffffff;
  width: 12px;
  height: 12px;
  display: block;
  border-radius: 50%;
  transition: 0.3s ease;
}
.pagination span.swiper-pagination-bullet-active {
  background: #B21403;
  border-color: transparent !important;
}

.content-anim,
.content-anim-before::before {
  transition: 0.4s ease;
  transform-box: fill-box;
  transform: translateX(-100%);
  opacity: 0;
}
.content-anim_step2,
.content-anim-before::before_step2 {
  transition-delay: 0.2s;
}
.content-anim_step3,
.content-anim-before::before_step3 {
  transition-delay: 0.4s;
}
.content-anim_step4,
.content-anim-before::before_step4 {
  transition-delay: 0.6s;
}

.swiper-slide-active .content-anim,
.swiper-slide-active .content-anim-before::before {
  transform: translateX(0);
  opacity: 1;
}

.marker {
  position: absolute;
  pointer-events: none;
  width: 40px;
  height: 40px;
  left: 0px;
  top: 0px;
  background: #B21403;
}

.tag {
  font-size: 12px;
  background: #13385D;
  border: 0.5px solid #FFFFFF;
  border-radius: 100ch;
  padding: 9px 14px;
}

.title {
  font-weight: 800;
  font-size: clamp(36px, 1.455rem + 3.18vw, 64px);
  color: #1F5A95;
}

.stage {
  position: relative;
  background: #FFFFFF;
  border: 2px solid #AEAEAE;
  padding: 40px 16px;
  border-radius: 10px;
}
.stage__icon {
  position: absolute;
  top: 12px;
  right: 7px;
  background: #1F5A95;
  border-radius: 10px;
  display: inline-flex;
  padding: 10px;
}
.stage__icon img {
  object-fit: contain;
  object-position: center;
  width: clamp(25px, 0.852rem + 2.84vw, 50px);
  height: clamp(25px, 0.852rem + 2.84vw, 50px);
}
.stage__title {
  font-weight: 600;
  font-size: 24px;
  color: #1F5A95;
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}
.stage__title b {
  font-weight: 800;
  font-size: 37px;
  margin-bottom: 20px;
}
.stage__images {
  margin-top: 40px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 6px 8px;
}

.profit-item {
  display: flex;
  flex-direction: column;
}
.profit-item__title {
  font-weight: 600;
  font-size: 16px;
  color: #1F5A95;
  margin-bottom: 2em;
}
.profit-item__image {
  margin-top: auto;
  position: relative;
  display: flex;
  justify-content: center;
}
.profit-item__image::before {
  position: absolute;
  content: "";
  bottom: 0;
  left: 0;
  right: 0;
  height: 14%;
  border-radius: 10px 10px 0 0;
  background: #1F5A95;
}
.profit-item__image img {
  position: relative;
  height: auto;
}
.profit-item__colls {
  text-align: left;
  background: #1F5A95;
  padding: 35px 12px 40px;
  color: white;
  display: grid;
  grid-template-columns: 1fr auto 1fr;
}
.profit-item__colls::before {
  content: "";
  border-right: 2px solid white;
  width: 0;
  margin: 0 8px;
  margin-bottom: -8px;
  margin-top: 8px;
}
.profit-item__coll:nth-child(1) {
  order: -1;
}
.profit-item__coll-label {
  font-weight: 600;
  font-size: 16px;
  margin-bottom: 24px;
}
.profit-item__coll-price {
  font-weight: 600;
  font-size: 21px;
  margin-bottom: 18px;
}
.advant-item {
  position: relative;
  background: white;
  padding-bottom: clamp(60px, 1.96rem + 7.16vw, 123px);
  padding-top: clamp(30px, 0.739rem + 4.55vw, 70px);
  padding-right: clamp(70px, 1.676rem + 10.8vw, 165px);
  padding-left: clamp(30px, 0.739rem + 4.55vw, 70px);
  border-radius: clamp(30px, 0.739rem + 4.55vw, 70px) 0 0 0;
}
.advant-item::before {
  position: absolute;
  content: "";
  height: auto;
  background: url(../img/icons/check.webp) no-repeat center, #1F5A95;
  background-size: contain;
  aspect-ratio: 1/1;
  width: clamp(30px, 0.597rem + 5.11vw, 75px);
  border: clamp(2px, -0.244rem + 1.48vw, 15px) solid #1F5A95;
}
.advant-item:nth-of-type(1)::before {
  left: 0;
  bottom: 0;
}
.advant-item:nth-of-type(2) {
  border-radius: 0 clamp(30px, 0.739rem + 4.55vw, 70px) 0 0;
  padding-left: clamp(70px, 1.676rem + 10.8vw, 165px);
  padding-right: clamp(30px, 0.739rem + 4.55vw, 70px);
}
.advant-item:nth-of-type(2)::before {
  right: 0;
  bottom: 0;
}
.advant-item:nth-of-type(3) {
  border-radius: 0 0 0 clamp(30px, 0.739rem + 4.55vw, 70px);
  padding-top: clamp(60px, 1.96rem + 7.16vw, 123px);
  padding-bottom: clamp(30px, 0.739rem + 4.55vw, 70px);
}
.advant-item:nth-of-type(3)::before {
  left: 0;
  top: 0;
}
.advant-item:nth-of-type(4) {
  border-radius: 0 0 clamp(30px, 0.739rem + 4.55vw, 70px) 0;
  padding-top: clamp(60px, 1.96rem + 7.16vw, 123px);
  padding-bottom: clamp(30px, 0.739rem + 4.55vw, 70px);
  padding-left: clamp(70px, 1.676rem + 10.8vw, 165px);
  padding-right: clamp(30px, 0.739rem + 4.55vw, 70px);
}
.advant-item:nth-of-type(4)::before {
  top: 0;
  right: 0;
}
@media (max-width: 800px) {
  .advant-item {
    padding: 30px !important;
  }
}
@media (max-width: 500px) {
  .advant-item {
    padding: 15px !important;
  }
}
.advant-item p, .advant-item span, .advant-item b {
  position: relative;
  z-index: 20;
  text-shadow: -1px -1px 0 white, 1px -1px 0 white, -1px 1px 0 white, 1px 1px 0 white;
}
.advant-item__title {
  font-weight: 600;
  font-size: clamp(16px, 0.773rem + 0.91vw, 24px);
  color: #1F5A95;
  margin-bottom: 7px;
}
.advant-item__descr {
  font-size: clamp(11px, 0.489rem + 0.8vw, 18px);
}

.main {
  position: relative;
  background-color: #7C6A6C;
  background: url(../img/bg/main.webp) left top no-repeat;
  color: #FFFFFF;
  padding: 36px 0;
}
.main__bg {
  pointer-events: none;
  position: absolute;
  inset: 0;
}
.main__bg span {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  transition: 0.4s ease;
  transform-box: view-box;
  transform-origin: left;
  transform: scaleX(0);
}
.main__bg span:nth-child(1) {
  width: 79%;
  background: rgba(31, 90, 149, 0.35);
  border-top-right-radius: 8% 50%;
  border-bottom-right-radius: 8% 50%;
}
.main__bg span:nth-child(2) {
  width: 87%;
  background: rgba(138, 184, 230, 0.16);
  border-top-right-radius: 9% 50%;
  border-bottom-right-radius: 7% 50%;
  transition-delay: 0.2s;
}
.main__bg span:nth-child(3) {
  width: 74%;
  background: rgba(19, 56, 93, 0.4);
  border-top-right-radius: 13% 50%;
  border-bottom-right-radius: 10% 50%;
  transition-delay: 0.4s;
}
.main__bg span:nth-child(4) {
  width: 62%;
  background: rgba(19, 56, 93, 0.39);
  border-top-right-radius: 19% 50%;
  border-bottom-right-radius: 13% 50%;
  transition-delay: 0.6s;
}
@media (max-width: 670px) {
  .main__bg span {
    top: auto;
    right: 0;
    transform: scaleY(0);
  }
  .main__bg span:nth-child(1) {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    width: auto;
    height: 79%;
    background: rgba(31, 90, 149, 0.35);
    border-top-left-radius: 50% 8%;
    border-top-right-radius: 50% 8%;
  }
  .main__bg span:nth-child(2) {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    width: auto;
    height: 87%;
    background: rgba(138, 184, 230, 0.16);
    border-top-left-radius: 50% 9%;
    border-top-right-radius: 50% 7%;
  }
  .main__bg span:nth-child(3) {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    width: auto;
    height: 74%;
    background: rgba(19, 56, 93, 0.4);
    border-top-left-radius: 50% 13%;
    border-top-right-radius: 50% 10%;
  }
  .main__bg span:nth-child(4) {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    width: auto;
    height: 62%;
    background: rgba(19, 56, 93, 0.39);
    border-top-left-radius: 50% 19%;
    border-top-right-radius: 50% 13%;
  }
}
.main.swiper-slide-active .main__bg span {
  transform: scaleX(1);
}
@media (max-width: 670px) {
  .main.swiper-slide-active .main__bg span {
    transform-origin: bottom;
    transform: scaleY(1);
  }
}
.main__container {
  position: relative;
  display: flex;
  flex-direction: column;
}
@media (max-width: 670px) {
  .main__container {
    align-items: center;
    text-align: center;
  }
}
.main__tags {
  display: flex;
  gap: 6px 10px;
  flex-wrap: wrap;
  margin-bottom: auto;
}
@media (max-width: 670px) {
  .main__tags {
    justify-content: center;
  }
}
.main__title {
  font-weight: 800;
  font-size: clamp(48px, 1.636rem + 5.45vw, 96px);
  margin-bottom: 26px;
  margin-top: 41px;
  color: white;
}
.main__subtitle {
  font-weight: 600;
  font-size: clamp(24px, 1.273rem + 0.91vw, 32px);
  margin-bottom: 41px;
}
.main__text {
  margin-bottom: 41px;
}
.main__list {
  display: flex;
  flex-direction: column;
  margin-bottom: auto;
  gap: 16px;
  text-align: left;
}
.main__list li {
  font-size: 19px;
  line-height: 150%;
  display: flex;
  align-items: center;
  gap: 9px;
}
.main__list li img {
  object-fit: contain;
  object-position: center;
  width: 22px;
}
@media (max-height: 720px) {
  .main__list {
    margin-bottom: 12px;
  }
}
.main__list-line {
  margin-bottom: 26px;
  height: 0;
  display: block;
  border: 1px solid #A5A5A5;
  max-width: 407px;
  width: 100%;
  position: relative;
}
.main__list-line::before {
  position: absolute;
  content: "";
  left: 0;
  width: 28%;
  height: 0px;
  border: 6.66667px solid #B21403;
  transform: translateY(-1px) translateY(-50%);
}

.about {
  display: flex;
  flex-direction: column;
}
.about__top {
  position: relative;
  flex: 1 0 auto;
}
.about__top [class*=__container] {
  position: relative;
  display: flex;
  align-items: flex-start;
  gap: 5px;
}
@media (max-width: 1100px) {
  .about__top [class*=__container] {
    flex-direction: column;
    text-align: center;
    align-items: center;
  }
}
@media (max-width: 550px) {
  .about__top [class*=__container] {
    align-items: center;
    text-align: center;
  }
}
.about__top::before {
  transform: translateX(100%);
  content: "";
  pointer-events: none;
  position: absolute;
  width: 33%;
  right: 0;
  top: 0;
  bottom: 0;
  background: url(../img/bg/about.webp) center no-repeat;
  background-size: cover;
}
@media (max-width: 1100px) {
  .about__top::before {
    height: 33%;
    top: auto;
    left: 0;
    width: auto;
  }
}
@media (max-width: 550px) {
  .about__top::before {
    height: 20%;
  }
}
.about__main {
  max-width: 655px;
  padding: 53px 0 40px;
}
.about__title {
  margin-bottom: 39px;
}
.about__descr {
  font-weight: 600;
  font-size: 18px;
  line-height: 100%;
  color: #000000;
  margin-bottom: 27px;
}
.about__grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  text-align: left;
  gap: 12px;
}
@media (max-width: 550px) {
  .about__grid {
    display: flex;
    flex-direction: column;
  }
}
.about__grid li {
  display: flex;
  flex-direction: column;
}
@media (max-width: 550px) {
